@font-face {
  font-family: Lato-bold;
  src: url("../../fonts/Lato/Lato-Bold.ttf") format("trueType");
  font-weight: 400;
  font-style: normal;
}
.flex-row {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
@media (max-width: 768px) {
  .media-font {
    font-size: 4vw;
  }
}
@media (max-width: 468px) {
  .media-font {
    font-size: 6vw;
  }
}
header {
  background-image: url(../../images/header/bg01.jpg);
  font-family: "Lato-bold";
  min-height: 400px;
  padding: 20px 0;
  overflow-x: hidden;
}
header .top-header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
header .top-header .logo {
  width: 15%;
  background-image: url(../../images/header/logo.png);
  background-repeat: no-repeat;
  background-size: contain;
}
header .top-header .logo p {
  font-size: 30px;
  color: #2d3f54;
}
@media (max-width: 768px) {
  header .top-header .logo p {
    font-size: 4vw;
  }
}
@media (max-width: 468px) {
  header .top-header .logo p {
    font-size: 6vw;
  }
}
header .top-header .logo p span {
  text-transform: uppercase;
}
header .top-header .menu {
  width: 80%;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}
header .top-header .menu nav ul li {
  float: left;
  margin-left: 4vw;
}
@media (max-width: 768px) {
  header .top-header .menu nav ul li {
    float: none;
  }
}
header .top-header .menu nav ul li a {
  padding: 2vw 1vw;
  position: relative;
  font-size: 20px;
}
header .top-header .menu nav ul li a:after {
  content: " ";
  height: 3px;
  position: absolute;
  width: 100%;
  background-color: #10b1b3;
  top: 0;
  left: 0;
}
header .header-banner {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  text-align: center;
}
header .header-banner h1 {
  font-size: 120px;
}
@media (max-width: 768px) {
  header .header-banner h1 {
    font-size: 9vw;
  }
}
header .header-banner button {
  background-color: #10b1b3;
  border: none;
  color: #fff;
  width: 40%;
  height: 60px;
  margin-top: 30px;
  cursor: pointer;
}
header .header-banner button:hover {
  background-color: #2d3f54;
}
header .header-banner p,
header .header-banner button {
  text-transform: uppercase;
  font-size: 18px;
  letter-spacing: 1px;
}
